		color:#ff5f00;
		border-bottom:none;
		border-left:3px solid #ff5f00;
	}
	div{
		float:left;
	}
	#right{
		float:right;
		width:75%;
		height:85vh;
	}
	ul{
    	width:100%;
    }
    li{
    	width:49%;
    	float:left;
    }
    ul li:nth-of-type(n){
    	margin-right:1%;
    }
    img{
    	width:100%;
    }
    .des{
    	font-size:12px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp:2;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all;
        color:black;
        height:32px;
    }
    span{
    	font-size:0.293333rem;
    	color:grey;
    }
    .price{
    	font-weight:bold;
    	color:#ff4c00;
    }
    .no7 li{
    	width:32.34%;
    }
    .no7 span{
    	font-size:12px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp:1;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all;
        color:black;
        text-align:center;
    }
</style>


